
import { defineComponent } from 'vue'
import { useI18n } from 'vue-i18n'

export default defineComponent({
  name: 'AppHome',
  setup() {
    const { t } = useI18n()
    return { t }
  },
  methods: {
    setupGame() : void {
      this.$store.commit('resetGame')
      this.$router.push('/setup/gameDifficulty')
    }
  }
})
